:app-content {
  --widthScaleRatio: 1;
  --heightScaleRatio: 1;
}

.contact-page-form {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.contact-page-form-checkbox {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 2rem;
}

.contact-page-form-header {
  font-size: 1.5rem;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
}

h1 {
  font-size: 2rem;
  margin: 0;
  margin-top: 5rem;
}

h2 {
  font-size: 1.25rem;
  color: white;
  margin-bottom: 5rem;
}

button {
  padding: 1rem calc(var(--widthScaleRatio) * 10rem);
  background-color: red;
  border: none;
  border-radius: 2rem;
  color: white;
  font-size: 2rem;
  font-weight: 1000;
}

button:hover {
  cursor: pointer;
  outline: yellow 0.15rem solid;
  color: yellow;
}
