:app-content {
  --widthScaleRatio: 1;
  --heightScaleRatio: 1;
}

.input-field-input {
  width: 100%;
  height: 5rem;
  border: none;
  border-radius: 0.5rem;
  padding: 0 1rem;
  font-size: 1.5rem;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
}

.input-field-container {
  margin-bottom: calc(var(--heightScaleRatio) * 2rem);
  height: 5rem;
  width: calc(var(--widthScaleRatio) / var(--heightScaleRatio) * 50rem);
}
